import React from 'react'
import { Header as Hdr, Box, Image } from 'grommet'
import colors from 'styles/colors'
import borders from 'styles/borders'
import PropTypes from 'prop-types'

const styles = {
  main: {
    width: '100%',
    height: '100%',
  },
  img: {
    borderLeft: borders.menu,
    height: 80,
    width: 80,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    cursor: 'pointer',
  },
}

const Img = ({ name }) => {
  return (
    <Box style={styles.img}>
      <Image src={`/images/${name}.svg`} />
    </Box>
  )
}

const Header = ({ style, children, isAuth, ...props }) => {
  return (
    <div style={styles.main}>
      <Hdr
        style={{ ...style, height: '100%', paddingRight: 0 }}
        {...props}
        background={colors.transparent}
        pad='small'>
        <div className='m-l-32'>
          <Image fit='cover' src='/images/logo.svg' />
        </div>
        {isAuth && (
          <Box direction='row'>
            <Img name='search' />
            <Img name='alerts' />
            <Img name='profile' />
          </Box>
        )}
      </Hdr>
    </div>
  )
}

Header.propTypes = {
  children: PropTypes.node,
  style: PropTypes.object,
  isAuth: PropTypes.bool,
}

export default Header
