import React, { useState } from 'react'
// import PropTypes from 'prop-types'
import SideMenuItem from './sideMenu/SideMenuItem'
import initialItems from './sideMenu/side-items'
import {
  Schedule,
  Equipment,
  AlertsSide,
  Contacts,
  Document,
} from './sideMenu/icons'
import colors from 'styles/colors'

const styles = {
  list: {
    listStyleType: 'none',
    margin: 0,
    padding: 0,
  },
}

const getIcon = (isActive, svgName) => {
  const fillColor = !!isActive ? colors.red : colors.greyDark
  if (svgName === 'schedule') {
    return <Schedule fill={fillColor} />
  } else if (svgName === 'equipment') {
    return <Equipment fill={fillColor} />
  } else if (svgName === 'contacts') {
    return <Contacts fill={fillColor} />
  } else if (svgName === 'document') {
    return <Document fill={fillColor} />
  } else {
    return <AlertsSide fill={fillColor} />
  }
}

const SideMenu = () => {
  const [items, setItems] = useState(initialItems)

  const handleClick = id => {
    const itm = [...items]
    itm.forEach(x => (x.isActive = x.id === id))
    setItems(itm)
  }

  return (
    <ul style={styles.list}>
      {items.map(x => {
        return (
          <li key={x.id} onClick={() => handleClick(x.id)}>
            <SideMenuItem name={x.name} isActive={x.isActive} icon={x.svg}>
              {getIcon(x.isActive, x.svg)}
            </SideMenuItem>
          </li>
        )
      })}
    </ul>
  )
}

// SideMenu.propTypes = {}

export default SideMenu
